<template>
  <div class="hello">
    <b-container fluid class="nopad">
    <h1>Identification of venomous snakes in Sri Lanka using Machine Learning</h1>
    

      <b-card
          title="Upload Image"
          img-src="/cover.jpg"
          img-alt="Image"
          img-top
          tag="article"
          style="max-width: 20rem; margin: auto; margin-top:20px; margin-bottom:20px;"
          
  >
    <b-card-text>
      Upload an image of the snake to identify
    </b-card-text>
    <b-img :src='preview' fluid alt="Responsive image" v-if='preview' thumbnail class="mt-3"></b-img>
    <hr>
    <b-form-file
      v-if='!analysing'
      v-model="file"
      :state="Boolean(file)"
      placeholder="Choose a file or drop it here..."
      drop-placeholder="Drop file here..."
      class="ov"
      name='file'
      @change="onFileChange"
    ></b-form-file>
   
   <div v-show=file>
      <b-button variant="primary" class="m-3" v-on:click='upload' v-if='!analysing'>Upload and analyze</b-button>
     <b-progress :value="value" :max="max" show-progress animated v-if='uploading'></b-progress>
   </div>

<hr>
   <div v-if='analysing'>
     <h5>Analysing</h5>
     <b-spinner type="grow" label="Loading..."></b-spinner>
   </div>

  <b-card v-if='analysed'
    
  >
    <b-card-title>Analysis report</b-card-title>
    <b-card-text>
      Prediction :<span class="bold"> {{analysis.displayName}}</span>  <br>
      Score: {{(analysis.classification.score * 100).toFixed(2)}} %
    </b-card-text>
  </b-card>

  </b-card>

    </b-container>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    
  },
  data(){
    return {
      file:null,
      value:0,
      max:100,
      server: window.location.origin,
      uploading : false,
      analysing:false,
      analysed: false,
      analysis:{},
      preview:null
     }
  },
  methods: {
    upload: function(){
      this.uploading = true
      this.analysed = false
      console.log(this.file)
      var vm = this
      var formData = new FormData();
      formData.append('file', this.file);
      this.axios.post(this.server + '/publicUpload', formData,{
            headers: {
              'Content-Type': 'multipart/form-data'
            },
            onUploadProgress: function(ev){
              vm.value = parseInt((ev.loaded / ev.total) * 100)
            }
          }).then(function(data){
            vm.uploading = false
            vm.per = 0
            vm.analysing = true
            console.log(data.data.filename)
            vm.analyseFile(data.data.filename)
          }).catch(function(err){
            console.log(err)
            vm.uploading = false
          });
    },
    // eslint-disable-next-line no-unused-vars
    analyseFile :  function(filename){
      var vm = this
      this.axios.get(`${vm.server}/predict?file=${filename}`).then(function(data){
        console.log(data);
        vm.analysis = data.data
        vm.analysed = true
        vm.analysing = false
      }).catch(function(err){
        console.log(err)
        vm.analysed = true
        vm.analysing = false
      });
    },
    onFileChange : function(e){
      var file = e.target.files[0];
      console.log( URL.createObjectURL(file))
      this.preview = URL.createObjectURL(file)
    }
  }
}
</script>

